import Utils from "Common/utils"
import "whatwg-fetch"
import "./extension"
const timeout = 300000
export default class HttpUtil {
  /**
   * get 
   * @param url
   * @param params
   * @param isHandleError
   * @param httpCustomerOpertion , 
   * {
   *    isHandleResult: boolen    //   true /false 
   *    isShowLoading: boolen     //loading
   *    customHead: object        // 
   *    timeout: int              //
   * }
   * @returns {Promise}
   */
  static get(url, params = {}, httpCustomerOpertion = { isHandleResult: true, isShowLoading: true }) {
    const method = "GET"
    if (!params.webMonitorId) {
      params.webMonitorId = window.localStorage.chooseWebMonitorId
    }
    const fetchUrl = url + Utils.qs(params)
    const fetchParams = Object.assign({}, { method }, this.getHeaders())
    return HttpUtil.handleFetchData(fetchUrl, fetchParams, httpCustomerOpertion)
  }

  /**
   * post 
   * @param url
   * @param params
   * @param isHandleError
   * @param httpCustomerOpertion , 
   * @returns {Promise}
   */
  static post(url, params = {}, httpCustomerOpertion = { isHandleResult: true, isShowLoading: true }) {
    const method = "POST"
    if (!params.webMonitorId) {
      params.webMonitorId = window.localStorage.chooseWebMonitorId
    }
    const body = JSON.stringify(params)
    const fetchParams = Object.assign({}, { method, body }, this.getHeaders())
    return HttpUtil.handleFetchData(url, fetchParams, httpCustomerOpertion)
  }

  /**
   * put 
   * @param url
   * @param params
   * @param isHandleError
   * @param httpCustomerOpertion , 
   * @returns {Promise}
   */
  static put(url, params = {}, httpCustomerOpertion = { isHandleResult: true, isShowLoading: true  }) {
    const method = "PUT"
    params.webMonitorId = window.localStorage.chooseWebMonitorId
    const body = JSON.stringify(params)
    const fetchParams = Object.assign({}, { method, body }, this.getHeaders())
    return HttpUtil.handleFetchData(url, fetchParams, httpCustomerOpertion)
  }

  /**
   * fetch
   * @param fetchUrl
   * @param fetchParams
   * @returns {Promise}
   */
  static handleFetchData(fetchUrl, fetchParams, httpCustomerOpertion) {
    // base64ios
    // TODO: debugPanelreact
    const logParams = { ...fetchParams }
    if (logParams.body && logParams.body.length > 1024) {
      logParams.body = logParams.body.substr(0, 1024) + "..."
    }
    const { isShowLoading } = httpCustomerOpertion
    if (isShowLoading) {
      HttpUtil.showLoading()
    }
    httpCustomerOpertion.isFetched = false
    httpCustomerOpertion.isAbort = false
    // 
    if (httpCustomerOpertion.hasOwnProperty("customHead")) {
      const { customHead } = httpCustomerOpertion
      fetchParams.headers = Object.assign({}, fetchParams.headers, customHead)
    }
    const fetchPromise = new Promise((resolve, reject) => {
      fetch(fetchUrl, fetchParams).then(
        response => {
          if (httpCustomerOpertion.isAbort) {
            // 
            return
          }
          if (isShowLoading) {
            HttpUtil.hideLoading()
          }
          httpCustomerOpertion.isFetched = true
          response.json().then(jsonBody => {
            if (response.ok) {
              if (jsonBody.status) {
                // 
                reject(HttpUtil.handleResult(jsonBody, httpCustomerOpertion))
              } else {
                resolve(HttpUtil.handleResult(jsonBody, httpCustomerOpertion))
              }
            } else {
              // http status header <200 || >299
              let msg = ""
              if (response.status === 404) {
                msg = ""
              }
              console.error(msg)
              reject(HttpUtil.handleResult({ fetchStatus: "error", netStatus: response.status }, httpCustomerOpertion))
            }
          }).catch(e => {
            const errMsg = e.name + " " + e.message
            reject(HttpUtil.handleResult({ fetchStatus: "error", error: errMsg, netStatus: response.status }, httpCustomerOpertion))
          })
        }
      ).catch(e => {
        const errMsg = e.name + " " + e.message
        if (httpCustomerOpertion.isAbort) {
          // 
          return
        }
        if (isShowLoading) {
          HttpUtil.hideLoading()
        }
        httpCustomerOpertion.isFetched = true
        if (httpCustomerOpertion.isHandleResult === true) {
          console.error("", 2)
        }
        reject(HttpUtil.handleResult({ fetchStatus: "error", error: errMsg }, httpCustomerOpertion))
      })
    })
    return Promise.race([fetchPromise, HttpUtil.fetchTimeout(httpCustomerOpertion)])
  }

  /**
   * 
   * @param result
   * ps:  this.isHandleError fetch
   */
  static handleResult(result, httpCustomerOpertion) {
    if (result.status && httpCustomerOpertion.isHandleResult === true) {
      const errMsg = result.msg || result.message || ""
      console.error(`${errMsg}${result.status}`, 2)
    }
    return result
  }
  /**
   * Fetch
   * @returns {Promise}
   */
  static fetchTimeout(httpCustomerOpertion) {
    const { isShowLoading } = httpCustomerOpertion
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        if (!httpCustomerOpertion.isFetched) {
          // fetch
          httpCustomerOpertion.isAbort = true
          if (isShowLoading) {
            HttpUtil.hideLoading()
          }
          console.error("", 2)
          reject({ fetchStatus: "timeout" })
        }
      }, httpCustomerOpertion.timeout || timeout)
    })
  }

  /**
   * 
   * @returns {{app-info: string, access-token: string}}
   */
  static getHeaders() {
    // app
    const fetchCommonParams = {
      // "mode": "cors",
      // "credentials": "same-origin"
    }
    const headers = {
      // "Accept": "*/*",
      // "Content-Type": "application/json;charset=utf-8",
    }
    return Object.assign({}, fetchCommonParams, { headers })
  }

  /**
   * loadding
   */
  static showLoading() {
    // console.log("...")
  }

  /**
   * loadding
   */
  static hideLoading() {
    // console.log("...")
  }

  // websocket 
  static wsGet(url, send, onmessage, onopen, onclose, onerror) {
      window.WebSocket = window.WebSocket || window.MozWebSocket
      // 
      if (!window.WebSocket) {
          console.error(": websocket")
          return
      }
      let socket = new WebSocket(url) // 
      socket.onmessage = (e) => { if (onmessage) onmessage(e) }
      socket.onopen = (e) => {
        console.log(url + " ")
        if (onopen) onopen(e)
        if (send) send(socket)
     }
      socket.onclose = (e) => {
        if (onclose) onclose(e)
        socket.close()
        socket = null // 
        console.log(url + " ")
      }
      socket.onerror = (e) => {
        console.log(e)
        if (onerror) onerror(e)
      }
  }

  /**
     * ajax
     * @param method 
     * @param url url
     * @param params 
     *        params.data.widthoutToken   true:token, false/undefined:token, token
     *        params.failCallback  result.status == 1 , 
     *        params.errorCallback(errorType, xhr)  ,, err
     *        params.completeCallback(errorType, xhr)  ,
     * @param successCallback  
     * @param closeLoadingImmediate  loading
     * @param showLoading loadding
     */
    static ajax(method, url, params, successCallback, failCallback) {
      const param = {
          url: url,
          type: method,
          data: JSON.stringify(params),
          Accept: "*/*",
          contentType: "application/json;charset=utf-8",
          dataType: "json",
          traditional: true,
          timeout: 10000,
          success: function(result, status, xhr) {
              try {
                  successCallback(result, xhr)
              } catch (e) {
                  console.error(e)
              }
          },
          error: function(xhr, errorType) {
              let msg = ""
              let res
              switch (xhr.status) {
                  case 404:
                      msg = ""
                      break
                  default:
                      switch (errorType) {
                          case "timeout":
                              msg = ""
                              break
                          case "abort":
                              msg = ""
                              break
                          case "error":
                              msg = ""
                              break
                          default:
                              res = xhr.responseText ? JSON.parse(xhr.responseText) : {}
                              msg = res.msg || (res.error || {}).message || ""
                      }
              }
              //,,xhr,errorType
              if (failCallback && typeof failCallback === "function") {
                failCallback(msg)
              }
              console.error(msg)
          }
      }
      $.ajax(param)
  }

  /**
   * 
   */
  static getApiDataFromCache(url) {
    if ("caches" in window) {
      console.log(window.caches)
      return caches.match(url).then((cache) => {
        console.log(3, cache)
        if (!cache) {
          return null
        }
        return cache.json()
      })
    }
    return Promise.resolve()
  }
}


